  margin: 0;
}

.main-page__body--with-menu {
  overflow: hidden;
}

.main-page__menu {
  opacity: 0;
  pointer-events: none;
  transition: translate .3s linear, opacity .3s linear;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: auto;
  transform: translateX(-100%);
}

